import { Component, OnInit } from '@angular/core';
import { Body } from '@angular/http/src/body';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {
  name:string;
  age:number;
  address:Address;
  hobbies:string[];
  email:string;
  isEdit:boolean;
  constructor() { 

  }

  ngOnInit() {
    this.name ='kp';
    this.age=2;
    this.email="";
    this.address ={
      street:"7th main",
      city:"indore",
      state:"mp"
    }
    this.isEdit=false;
    this.hobbies=['core','game','movie'];
  }
  addHobby(hobby){
    console.log(hobby);
    this.hobbies.unshift(hobby);
    return false;
  }
  deleteHobby(hobby){
    console.log(hobby);
    for(let i =0;i<this.hobbies.length;i++){
      if(this.hobbies[i]==hobby){
        this.hobbies.splice(i,1);
      }
    }
    return false;

  }
  toggleEdit(){
    this.isEdit=!this.isEdit;
  }
  
  
}
interface Address{
street:string;
city:string;
state:string;

}




